.popup {
  background-color: rgba(0, 0, 0, 0.5);
  color: black;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  box-sizing: border-box;
  padding: 123px 20px;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s, opacity 0.5s ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 390px) {
  .popup {
    padding: 123px 40px;
  }
}